	import {
			mapState,  
		    mapMutations  
		} from 'vuex'; 
		
    export default {
        data() {
            return {
                msgTitles: ["小区公共设施毁坏无人维修", "小区卫生环境无人打扫", "小区保安服务态度极差", "平台界面显示错乱", "平台偶发性崩溃"],
                imageList: [],
                sendData: {
                    score: 0,
					title: "",
                    content: "",
                    contact: ""
                }
            }
        },
        onLoad() {
            let deviceInfo = {
                appid: plus.runtime.appid,
                imei: plus.device.imei, //设备标识
                p: plus.os.name === "Android" ? "a" : "i", //平台类型，i表示iOS平台，a表示Android平台。
                md: plus.device.model, //设备型号
                app_version: plus.runtime.version,
                plus_version: plus.runtime.innerVersion, //基座版本号
                os: plus.os.version,
                net: "" + plus.networkinfo.getCurrentType()
            }
            this.sendData = Object.assign(deviceInfo, this.sendData);
        },
		computed: {
			...mapState(['token', 'globalUserInfo'])
		},
        methods: {
            chooseMsg() { //快速输入
                uni.showActionSheet({
                    itemList: this.msgTitles,
                    success: (res) => {
                        this.sendData.title = this.msgTitles[res.tapIndex];
                    }
                })
            },
            send() { //发送反馈
				var that = this;
				if (this.sendData.title.length==0){
					uni.showToast({
						title: "请输入问题",
						icon: 'none'
					})
					return false;
				}
				if (this.sendData.content.length==0){
					uni.showToast({
						title: "请输入意见",
						icon: 'none'
					})
					return false;
				}
					
				uni.request({
					url: that.global_baseUrl + '/feedbackInfo/',
					data: {
						title: this.sendData.title,
						content: this.sendData.content,
						contact: this.sendData.contact,
					},
					method:"POST",
					header: {
						'Content-Type': 'application/x-www-form-urlencoded',
						'Authorization': 'Token  ' + this.token,
					},
					success: (res) => {
						if (res.data == "ok"){
							uni.showToast({
								title: "反馈成功!",
								icon: 'success'
							})
							setTimeout(() => {
								uni.switchTab({
									url: '/pages/tabbar/tabbar-1/home'
								})
							}, 1000)
						}
						setTimeout(function () {
							uni.hideLoading();
						}, 1000);
					}
				});
            }
        }
    }
